"use client";
import Dropdown from "@/app/dhannu/components/resuableComponents/Dropdown";
import { ProjectContext } from "@/app/dhannu/context/ProjectContext";
import { SpaceContext } from "@/app/dhannu/context/SpaceContext";
import { useParams } from "next/navigation";
import React, { useContext, useEffect, useState } from "react";
import { AiOutlineThunderbolt } from "react-icons/ai";
import { CiViewList } from "react-icons/ci";
import { FaFlipboard } from "react-icons/fa6";
import { FcCalendar } from "react-icons/fc";
import { GiArtificialHive } from "react-icons/gi";
import { MdSupportAgent } from "react-icons/md";

export default function Page() {
  const { todoId } = useParams();
  const { list } = useContext(SpaceContext);
  const { assignUser, priorityOptions, statusOptions } =
    useContext(ProjectContext);

  // Find the selected space
  const currentSpace = list.find((space) => space.id == todoId);
  const projects = currentSpace?.projects || [];

  useEffect(() => {
    console.log("Current space:", currentSpace);
    console.log("Projects:", projects);
  }, [currentSpace]);

  return (
    <div className="w-full h-full py-3 px-2">
      {/* -------- Header -------- */}
      <div className="w-full flex justify-between">
        <h1 className="text-xl text-zinc-200 font-semibold">
          Dhannu Workspace
        </h1>
        <div className="flex gap-6 text-[15px]">
          <div className="flex items-center gap-2">
            <MdSupportAgent size={17} /> Agents
          </div>
          <div className="flex items-center gap-2">
            <AiOutlineThunderbolt size={17} /> Automate
          </div>
          <div className="flex items-center gap-2">
            <MdSupportAgent size={17} /> Ask AI
          </div>
          <div className="flex items-center gap-2">
            <GiArtificialHive size={17} /> Share
          </div>
        </div>
      </div>

      {/* -------- Navigation Tabs -------- */}
      <div className="flex items-center gap-4 border-b border-gray-800 py-2 text-sm text-gray-300 mt-2">
        <div className="flex items-center gap-1 cursor-pointer hover:text-white">
          <CiViewList /> <span>List</span>
        </div>
        <div className="flex items-center gap-1 cursor-pointer hover:text-white">
          <FaFlipboard /> <span>Board</span>
        </div>
        <div className="flex items-center gap-1 cursor-pointer hover:text-white">
          <FcCalendar /> <span>Calendar</span>
        </div>
        <div className="flex items-center gap-1 border-l border-gray-700 pl-3 cursor-pointer hover:text-white">
          <span>+</span>
          <span>View</span>
        </div>
      </div>

      {/* -------- Project Table -------- */}
      <div className="w-full mt-6 border border-zinc-700 rounded-lg overflow-hidden">
        <div className="bg-zinc-800 text-white px-4 py-3 text-lg font-semibold">
          {currentSpace?.spaceList || "No Space Selected"}
        </div>

        <div className="overflow-x-auto bg-zinc-900">
          <table className="min-w-full text-sm text-gray-300">
            <thead className="bg-zinc-800 text-gray-200 border-b border-zinc-700">
              <tr>
                <th className="py-3 px-4 text-left">Project Name</th>
                <th className="py-3 px-4 text-left">Status</th>
                <th className="py-3 px-4 text-left">Assignee</th>
                <th className="py-3 px-4 text-left">Priority</th>
                <th className="py-3 px-4 text-left">End Date</th>
                <th className="py-3 px-4 text-left">Description</th>
              </tr>
            </thead>
            <tbody>
              {projects.length > 0 ? (
                projects.map((project) => (
                  <tr
                    key={project.id}
                    className="border-b border-zinc-700 hover:bg-zinc-800 transition"
                  >
                    <td className="py-2 px-4">{project.name}</td>
                    <td className="py-2 px-4">{project.status || "-"}</td>
                    <td className="py-2 px-4">{project.assignee || "-"}</td>
                    <td className="py-2 px-4">{project.priority || "-"}</td>
                    <td className="py-2 px-4">{project.endDate || "-"}</td>
                    <td className="py-2 px-4">{project.description || "-"}</td>
                  </tr>
                ))
              ) : (
                <tr>
                  <td
                    colSpan="6"
                    className="text-center py-3 text-gray-500 italic"
                  >
                    No projects found in this space
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
}
